@charset "UTF-8";
fieldset,
legend,
button {
  margin: 0;
  padding: 0;
  border: none;
  border-radius: 0;
  outline: none;
  background: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

input[type="date"],
input[type="datetime-local"],
input[type="month"],
input[type="time"] {
  margin: 0;
  padding: 4px;
  border: 1px solid #ccc;
  outline: none;
  font-size: 16px;
}

input[type="text"],
input[type="password"],
input[type="email"],
input[type="tel"],
input[type="url"],
input[type="search"],
input[type="number"],
input[type="datetime"],
input[type="week"],
textarea,
select {
  margin-right: 0;
  margin-left: 0;
  padding: 4px;
  border: 1px solid #ccc;
  border-radius: 0;
  outline: none;
  background: none;
  font-size: 16px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

textarea {
  width: 100%;
  height: 100px;
  overflow: auto;
}

select {
  padding-right: 30px;
  background-image: url("data:image/png;base64,");
  background-repeat: no-repeat;
  background-size: 12px 12px;
  background-position: right 8px center;
}

.select {
  display: inline-block;
  position: relative;
  overflow: hidden;
  background: #fff;
}

.select select {
  width: 100%;
  padding-right: 24px;
  border: 1px solid #ccc;
  background: none;
  text-overflow: "";
  cursor: pointer;
}

.select::before {
  position: absolute;
  top: 13px;
  right: 8px;
  width: 0;
  height: 0;
  padding: 0;
  content: "";
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-top: 6px solid #666;
  pointer-events: none;
}

/* radio & checkbox */
input[type="radio"],
input[type="checkbox"] {
  display: none;
}

input[type="radio"] + span,
input[type="checkbox"] + span {
  display: inline-block;
  position: relative;
  margin-left: 8px;
  padding: 10px 20px;
  color: #555;
  font-size: 1.4rem;
  text-align: center;
  line-height: 1;
  cursor: pointer;
}

input[type="radio"] + span::before,
input[type="checkbox"] + span::before {
  position: absolute;
  top: 50%;
  left: -8px;
  width: 16px;
  height: 16px;
  content: "";
  margin-top: -8px;
  border: 1px solid #ccc;
  background: #fff;
}

/* fieldset */
fieldset {
  padding: 8px 16px;
  border: 1px solid #ccc;
}

legend {
  padding: 0 8px;
}

/* button */
input[type="submit"],
input[type="reset"],
input[type="button"],
button {
  display: inline-block;
  margin: 0;
  padding: 10px 30px;
  border: 1px solid #999;
  border-radius: 0;
  -webkit-box-sizing: content-box;
  -webkit-appearance: button;
  appearance: button;
  background: #efefef;
  color: #000;
  font-size: 20px;
  cursor: pointer;
}

/* ブラウザ対策
---------------------------------------------------------------------------- */
/* Firefox */
/* IE */
select::-ms-expand {
  display: none;
}

/* webkit */
/* iOS */
input[type="submit"]::-webkit-search-decoration,
input[type=reset]::-webkit-search-decoration,
input[type="button"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-decoration {
  display: none;
}

::-webkit-search-cancel-button {
  -webkit-appearance: none;
}

input[type="search"]::-webkit-search-decoration {
  margin: 0;
  padding: 0;
  border: 0;
  border-radius: 0;
  -moz-appearance: none;
  -webkit-appearance: textfield;
  -webkit-box-sizing: border-box;
  appearance: none;
  background: none;
  outline: none;
  font-size: 16px;
}

/* 挙動
---------------------------------------------------------------------------- */
/* hover */
input:hover,
textarea:hover,
select:hover {
  border-color: #666;
}

input[type="radio"] + span:hover,
input[type="checkbox"] + span:hover {
  color: #000;
}

input[type="radio"] + span:hover::before,
input[type="checkbox"] + span:hover::before {
  border-color: #000;
}

input[type="radio"] + span:hover::after,
input[type="checkbox"] + span:hover::after {
  position: absolute;
  content: "";
  top: 50%;
  left: -4px;
  width: 8px;
  height: 8px;
  margin-top: -4px;
  background: #ccc;
}

/* checked */
input[type="radio"]:checked + span,
input[type="checkbox"]:checked + span {
  color: #3498db;
}

input[type="radio"]:checked + span::before,
input[type="checkbox"]:checked + span::before {
  border-color: #3498db;
}

input[type="radio"]:checked + span::after,
input[type="checkbox"]:checked + span::after {
  position: absolute;
  content: "";
  top: 50%;
  left: -4px;
  width: 8px;
  height: 8px;
  margin-top: -4px;
  background: #3498db;
}

/* radio */
input[type="radio"] + span::before,
input[type="radio"] + span:hover::after,
input[type="radio"]:checked + span::after {
  border-radius: 50%;
}

/* button */
input[type="submit"]:hover,
input[type="reset"]:hover,
input[type="button"]:hover,
button:hover {
  border-color: #3498db;
  background: #3498db;
  color: #fff;
}

/* focus */
input:focus,
textarea:focus {
  border-color: #3498db;
}

input[type="submit"]:focus,
input[type=reset]:focus,
input[type="button"]:focus,
input[type="search"]:focus {
  outline-offset: -2px;
}

/* disabled */
input:disabled,
input:disabled:hover,
textarea:disabled,
textarea:disabled:hover {
  border-color: #ccc;
  background: #eee;
  cursor: not-allowed;
}

input[type="radio"]:disabled + span,
input[type="checkbox"]:disabled + span {
  color: #ccc;
  cursor: not-allowed;
}

input[type="radio"]:disabled + span::before,
input[type="checkbox"]:disabled + span::before {
  border-color: #ccc;
  cursor: not-allowed;
}

/* バリデーション */
/* placeholder */
.l-header {
  width: 100%;
}

.l-header .container {
  background-color: #FFF;
  box-shadow: 0 3px 6px #00000029;
  height: 80px;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  z-index: 99;
}

.l-header .container .logo {
  height: 100%;
  width: 40%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.l-header .container .logo img {
  width: 140px;
  display: block;
}

@media screen and (max-width: 767px) {
  .l-header .container .navbtn {
    position: relative;
    z-index: 10;
    justify-content: center;
    display: flex;
    right: 0;
    width: 50px;
    height: 80px;
    margin-left: auto;
    box-sizing: border-box;
    background-color: #0C2E5A;
    border: none;
    cursor: pointer;
  }
  .l-header .container .navbtn.active {
    position: fixed;
    background-color: #FFF;
  }
  .l-header .container .navbtn.active .navbtn_txt_close {
    position: absolute;
    color: #0c2e5a;
    font-size: 10px;
    bottom: 5px;
  }
  .l-header .container .navbtn.active i {
    background-color: #0C2E5A;
  }
  .l-header .container .navbtn.active i:nth-child(1) {
    transform: translateY(9px) rotate(-45deg);
  }
  .l-header .container .navbtn.active i:nth-child(2) {
    opacity: 0;
  }
  .l-header .container .navbtn.active i:nth-child(3) {
    transform: translateY(-8px) rotate(45deg);
  }
  .l-header .container .navbtn .navbtn_txt_menu {
    position: absolute;
    color: #FFF;
    font-size: 10px;
    bottom: 5px;
  }
  .l-header .container .navbtn .navbtn_txt_close {
    font-size: 10px;
    color: transparent;
  }
  .l-header .container .navbtn i {
    display: block;
    width: 30px;
    height: 3px;
    background-color: #FFF;
    position: absolute;
    transition: transform 0.5s, opacity 0.5s;
  }
  .l-header .container .navbtn i:nth-child(1) {
    top: 25px;
  }
  .l-header .container .navbtn i:nth-child(2) {
    top: -10px;
    bottom: 0;
    margin: auto;
  }
  .l-header .container .navbtn i:nth-child(3) {
    bottom: 35px;
  }
}

.catch-copy {
  text-align: center;
  width: 53%;
  position: absolute;
  top: 38%;
  left: 0%;
}

.catch-copy .catch-copy__ja {
  font-size: 3.9vw;
  color: #0C2E5A;
  padding: 33px 9.9vh 30px;
  font-family: "Noto Sans JP", sans-serif;
  line-height: 1.4;
}

.catch-copy .catch-copy__ja .wo {
  font-size: 3.6vw;
}

.catch-copy .catch-copy__ja__back {
  background-color: rgba(255, 230, 33, 0.8);
}

.catch-copy .catch-copy__en {
  font-size: 1.5vw;
  color: #0C2E5A;
  padding: 10px 14.8vw;
}

.main-visual {
  position: relative;
  width: 100%;
}

.main-visual .main-visual__img1, .main-visual .main-visual__img2 {
  width: 100%;
  height: calc(100vh - 80px);
}

.main-visual .main-visual__img1 {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  animation-name: slide;
  animation-duration: 4s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}

.main-visual .main-visual__img2 {
  object-position: left;
}

.main-visual img {
  max-width: 100%;
  vertical-align: top;
  object-fit: cover;
}

@keyframes slide {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

/*
html5doctor.com Reset Stylesheet
v1.6.1
Last Updated: 2010-09-17
Author: Richard Clark - http://richclarkdesign.com
Twitter: @rich_clark
*/
html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

body {
  line-height: 1;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

nav ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

/* change colours to suit your needs */
ins {
  background-color: #ff9;
  color: #000;
  text-decoration: none;
}

/* change colours to suit your needs */
mark {
  background-color: #ff9;
  color: #000;
  font-style: italic;
  font-weight: bold;
}

del {
  text-decoration: line-through;
}

abbr[title], dfn[title] {
  border-bottom: 1px dotted;
  cursor: help;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* change border colour to suit your needs */
hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #cccccc;
  margin: 1em 0;
  padding: 0;
}

input, select {
  vertical-align: middle;
}

.top-btn {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: #0c2e5a;
  text-align: center;
  align-items: center;
  display: block;
  position: fixed;
  bottom: 20px;
  right: 20px;
}

.top {
  padding-top: 80px;
}
